            <td><nobr>{{row.defname}}</nobr></td>
            <td><nobr>{{row.f_user_name}}</nobr></td>
            <td><nobr>{{row.f_residential_area}}</nobr></td>
            <td><nobr>{{row.f_address}}</nobr></td>
            <td><nobr>{{row.f_apply_date}}</nobr></td>
            <td><nobr>{{row.f_apply_count}}</nobr> </td>
