          </tr>
        </template>
      </data-grid>
    </criteria-paged>
  </div>
</div>
</template>
<script>
import {
  PagedList
} from 'vue-client'
export default {
  title: '报建业务信息1',
  data () {
    return {
      showbtn: false,
      model: new PagedList('rs/sql/checkuser', 9, {data:{id: this.$login.f.id, fengongsi: this.$login.f.f_fengongsi, uid: '3'}})
    }
  },
  ready () {
    this.$refs.cp.$refs.cri.search()
    this.$refs.cp.pager = false
  },
  methods: {
    clifun(row) {
      this.$dispatch('showline',row)
    }
  }
}
</script>



